import sourceModule from 'vue-json-pretty'
import _ from 'lodash'
class Factory {
  /**
   * vue-json-pretty ,
   * @param {number} virtualLines  
   * @returns {object}  vue-json-pretty 
   */
  constructor({ virtualLines = 20 } = {}) {
    const instance = _.cloneDeep(sourceModule)

    instance.methods.onTreeScroll = function () {
      if (this.virtual) {
        const e = virtualLines,
          t = this.$refs.tree && this.$refs.tree.scrollTop || 0,
          o = Math.floor(t / this.itemHeight);
        let l = o < 0 ? 0 : o + e > this.flatData.length ? this.flatData.length - e : o;
        l < 0 && (l = 0);
        const n = l + e;
        this.translateY = l * this.itemHeight, this.visibleData = this.flatData.filter(((e, t) => t >= l && t < n))
      } else this.visibleData = this.flatData
    }
    return instance
  }
}

export default new Factory()

export {
  Factory
}