import { toHump } from '@/utils/format'

const requireModule = require.context('./', true, /index.vue$/)
const modules = {}
requireModule.keys().forEach(c => {
  const module = requireModule(c)
  const d = module.default
  if (d) {
    const name = toHump(c.replace('./', '').replace('/index.vue', ''))
    modules[name] = d
  }
})

//  attrVue vue

export default modules
